:root {
  --font-body: "Open Sans",Arial,sans-serif;
}

:root {
  --white: hsl(0,0%,100%);
  --black: hsl(0,0%,0%);
}

.flip-in {
  animation: flip-in 250ms;
  animation-fill-mode: forwards;
}

.flip-out {
  animation: flip-out 250ms;
  animation-fill-mode: forwards;
}

.jump {
  animation: jump 250ms;
  animation-fill-mode: forwards;
}

@keyframes flip-in {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(-90deg);
  }
}
@keyframes flip-out {
  from {
    transform: rotateX(-90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}
@keyframes jump {
  0% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(-0.625rem);
  }
  100% {
    transform: translateY(0rem);
  }
}
html {
  font-size: 100%;
  font-family: var(--font-body);
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0rem;
  padding: 1rem 2rem;
  background-color: hsl(240, 3%, 7%);
  color: hsl(0, 0%, 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
}

h1, h2, h3 {
  margin-top: 0rem;
  line-height: 1.1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0rem 1.5rem;
  max-width: 69.375rem;
  margin: 0rem auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0rem;
    transform: translate(0rem, 0rem);
  }
}

.guess {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.1875rem;
  width: 18.75rem;
  margin-bottom: 0.625rem;
}
.guess__tile {
  border: 0.125rem solid hsl(0, 0%, 26%);
  width: 3.25rem;
  height: 3.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: 600;
}
.guess .has-letter {
  border-color: hsl(0, 0%, 40%);
}
.guess .correct {
  background-color: hsl(120, 100%, 28%);
  border-color: hsl(120, 100%, 28%);
}
.guess .present {
  background-color: hsl(49, 69%, 47%);
  border-color: hsl(49, 69%, 47%);
}
.guess .absent {
  background-color: hsl(0, 0%, 35%);
  border-color: hsl(0, 0%, 35%);
}